import Vue from 'vue'
import VueRouter from 'vue-router'
import Todos from '../views/Todos.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/todos' },
  {
    path: '/todos',
    name: 'Todos',
    component: Todos,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
